body {
	margin: 0;
	padding: 0;
	font-family: Open Sans;
}

h1 {
	font-family: Open Sans Condensed;
}

h2 {
	font-size: 36px;
	color: #3c763d;
	color: var(--secondary);
	font-family: Open Sans Condensed;
	font-weight: 800;
}

h3 {
	font-family: Open Sans Condensed;
	font-size: 24px;
	color: #2e4468;
	color: var(--tertiary);
	font-weight: 800;
}

h4 {
	font-family: Open Sans Condensed;
	color: #ca5800;
	color: var(--quartary);
}

h5 {
	font-family: Open Sans Condensed;
}

.btn-primary {
	-webkit-filter: saturate(90%);
	        filter: saturate(90%);
	background-color: #005fd3;
	border-color: #005fd3;
}

.badge-success {
	background-color: #0a8927;
}

.nav-link {
	color: #0363c9;
}

.nav-pills .nav-link.active {
	background-color: #0070f4 !important;
}

.progress-bar {
	background-color: #0363c9;
}

label {
	font-weight: 600;
	font-stretch: extra-expanded;
	color: #2e4468;
	color: var(--primary);
}

.centered {
	display: block;
	margin: 0 auto;
}

.title {
	font-weight: 600;
	font-stretch: extra-expanded;
	color: #2e4468;
	color: var(--primary);
}

.empty-button {
	background: none;
	border: none;
	cursor: pointer;
}

#popover-contained {
	padding: 5px;
}

.modal-100w {
	max-width: none !important;
	width: 100%;
	margin: 0;
	padding: 0;
}

.modal-90w {
	max-width: none !important;
	width: 90%;
}

.modal-75w {
	max-width: none !important;
	width: 75%;
}

.modal-60w {
	max-width: none !important;
	width: 60%;
}

.modal-50w {
	max-width: none !important;
	width: 50%;
}

.modal-40w {
	max-width: none !important;
	width: 40%;
}

.modal-30w {
	max-width: none !important;
	width: 30%;
}

#popover-data .arrow {
	display: none;
}

.modal-close {
	font-weight: 700;
	line-height: 1;
}

a {
	color: #016ee3;
}

.badge-primary {
	background-color: #016ee3;
}

